import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './BasicSvgIcon.vue'
import icons from '@/icons'
import iconMeta from './iconMeta'

type FillType = 'fill' | 'original' | 'stroke'

@Component({
    name: 'BasicSvgIcon',
    mixins: [template]
})
export default class BasicSvgIcon extends Vue {
    //#region datas ******************************/
    protected fillType: FillType[] = ['fill']
    protected query = ''
    protected fillTypeList: FillType[] = ['fill', 'original', 'stroke']
    protected iconMeta = iconMeta
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get icons() {
        if (this.query) {
            return icons.filter(v => {
                let meta = iconMeta[v.name]
                let name = meta ? meta.name + v.name : v.name
                return name.includes(this.query)
            })
        }

        return icons
    }

    protected get isShowFill() {
        return this.fillType.includes('fill')
    }

    protected get isShowOriginal() {
        return this.fillType.includes('original')
    }

    protected get isShowStroke() {
        return this.fillType.includes('stroke')
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
